@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");

body {
  font-family: "Roboto", sans-serif !important;
  width: 100%;
  height: 100%;
  position: absolute;
  font-size: 18px;
  font-weight: 400;
  scroll-behavior: smooth;
  margin: 0;
  color: #231f20;
  background: #f2f1f6;
  transition: background 0.3s;
}

h1,h2,h3,h4,p,a{
  margin: 0;
  padding: 0;
  font-style: unset;
}

input, textarea, select, button{
  width: 100%;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  z-index: 1;
  font-size: 18px;
  appearance: none;
  font-family: "Roboto", sans-serif;
  color: #949398;
  background: none;
  resize: none;
  transition: color 0.3s;

  &:focus{
    color: #231f20;
  }

  &::-webkit-calendar-picker-indicator {
    display: none;
  }
}

iframe{
  border: none;
}

.font{
  &.bold{
    font-weight: bold;
  } 
  &.blue{
    color: #007aff;
  }
  &.red{
    color: #ff002f;
  }
  &.green{
    color: #67cd67;
  }
  &.black{
    color: #000;
  }
  &.right{
    text-align: right !important;
  }
  &.center{
    text-align: center;
  }
}

.border{
  border-radius: 10px;

  &.dontBottom{
    border-radius: 10px 10px 0 0;
  }
}

.flex{
  display: flex;  
  &.between{
    justify-content: space-between;
  }
  &.center{
    justify-content: center;
    align-items: center;
  }
}

.disabled{
  pointer-events: none;
  touch-action: none;
  color: #999999 !important;
}